const GAS_SIZE = '250000000000000';
const GAS_SIZE_CRON = '300000000000000';

export function NearContractApi({
  contract,
  ft,
  walletConnection,
  account,
  operationalCommission,
}) {
  const getAccount = async (accountId) => {
    try {
      if (!accountId) {
        return [];
      }
      return await contract.get_account({account_id: accountId});
    } catch (e) {
      console.log('near error', e);
    }
    return [];
  };

  const getCurrentAccount = () => getAccount(walletConnection.getAccountId());

  async function updateAccount() {
    const res = await contract.update_account(
      {
        account_id: account.accountId,
      },
      GAS_SIZE,
      operationalCommission,
    );
    return res;
  }

  async function changeAutoDeposit({streamId, autoDeposit}) {
    const res = await contract.change_auto_deposit(
      {stream_id: streamId, auto_deposit: autoDeposit},
      GAS_SIZE,
      operationalCommission,
    );

    return res;
  }

  async function depositStream({streamId, deposit}) {
    const res = await contract.deposit(
      {stream_id: streamId},
      GAS_SIZE,
      deposit,
    );

    return res;
  }

  async function pauseStream({streamId}) {
    const res = await contract.pause_stream(
      {stream_id: streamId},
      GAS_SIZE,
      operationalCommission,
    );

    return res;
  }

  async function startStream({streamId}) {
    const res = await contract.start_stream(
      {stream_id: streamId},
      GAS_SIZE,
      operationalCommission,
    );

    return res;
  }

  async function stopStream({streamId}) {
    const res = await contract.stop_stream(
      {stream_id: streamId},
      GAS_SIZE,
      operationalCommission,
    );
    return res;
  }

  async function createStream({
    deposit,
    ownerId,
    receiverId,
    token,
    speed,
    description,
    autoDepositEnabled = false,
    isAutoStartEnabled = true,
  }) {
    console.log('Creating stream', {
      deposit,
      ownerId,
      receiverId,
      token,
      speed,
      description,
      autoDepositEnabled,
      isAutoStartEnabled,
    });
    let res;
    if (token === 'NEAR') {
      res = await contract.create_stream(
        {
          owner_id: ownerId,
          receiver_id: receiverId,
          token_name: token,
          tokens_per_tick: speed,
          description,
          is_auto_deposit_enabled: autoDepositEnabled,
          is_auto_start_enabled: isAutoStartEnabled,
        },
        GAS_SIZE,
        deposit,
      );
    } else {
      const tokenContract = ft[token].contract;
      console.log(tokenContract, contract.contractId);
      try {
        res = await tokenContract.ft_transfer_call(
          {
            receiver_id: contract.contractId,
            amount: deposit,
            memo: 'Roketo transfer',
            msg: JSON.stringify({
              Create: {
                description: description,
                owner_id: ownerId,
                receiver_id: receiverId,
                token_name: token,
                tokens_per_tick: speed,
                balance: deposit,
                is_auto_deposit_enabled: autoDepositEnabled,
                is_auto_start_enabled: isAutoStartEnabled,
              },
            }),
          },
          GAS_SIZE,
          1,
        );
      } catch (error) {
        console.log('create FT error', error);
        throw error;
      }
    }
    return res;
  }

  async function getStream({streamId}) {
    const res = await contract.get_stream({
      stream_id: streamId,
    });

    return res;
  }

  async function change_auto_deposit({streamId, auto_deposit}) {
    const res = await contract.change_auto_deposit(
      {
        stream_id: streamId,
        auto_deposit: auto_deposit,
      },
      GAS_SIZE,
      operationalCommission,
    );

    return res;
  }

  async function getStreamHistory({streamId, from, to}) {
    const res = await contract.get_stream_history({
      stream_id: streamId,
      from: from,
      to: to,
    });

    return res;
  }

  async function startCron() {
    const res = await contract.start_cron(
      {},
      GAS_SIZE_CRON,
      operationalCommission,
    );

    return res;
  }

  /**
   * fetches general info about contract: Supported FT tokens, commision %
   */
  async function getStatus() {
    const res = await contract.get_status({});
    return res;
  }

  return {
    getStatus,
    // account methods
    getCurrentAccount,
    updateAccount,
    // stream methods
    getAccount,
    createStream,
    depositStream,
    pauseStream,
    startStream,
    stopStream,
    getStream,
    change_auto_deposit,
    getStreamHistory,
    changeAutoDeposit,
    // cron methods
    startCron,
  };
}
